body {
  box-sizing: border-box;
  --blue: #00acdc;
  --header-height: 60px;
  --after-height: 4px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navbar {
  height: var(--header-height);
}

.product-list {
  margin: auto 0;
  padding: 0;
  font-size: 0;
  list-style: none;
}

.product {
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

.product:first-of-type {
  margin-left: 0;
}

.nav-link {
  line-height: var(--header-height);
  color: #000;
  text-transform: uppercase;
  height: var(--header-height);
  width: 100%;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav-link:hover, .is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  height: var(--after-height);
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
}

